*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}

body {
  line-height: 1.6;
  font-weight: 400;
  color: $color-primary;
  font-family: $font-primary;
}

.container {
  display: grid;
  grid-template-columns: 3fr 1fr;

  @media screen and (max-width: 1300px) {
    & {
      display: flex;
      flex-direction: column;
      width: 100vw;
      height: 100vh;
    }
  }
}
